<template>
    <div>
        <identify-card-page
            v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"
            :disableGetUserId="true"
        />
        <input-info v-if="currentStep === steps.findIndex(step=>step.key === 'inputInfo')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="getResult"
            wait-text="正在查询中"
        />
        <result v-if="currentStep === steps.findIndex(step=>step.key === 'result')"/>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

import inputInfo from './components/input-info';
import result from './components/result';

import identifyCardPage from '@/view/public/before-work/identify-card-page';
import process from '@/components/process';
import { statisticalIdentity, checkSocialThird, checkSocialFifth } from '@/service/socialSecurity/accumulation';
import { cancelPrefix } from '@/utils/tools';

export default {
    name: 'AccumulationIndex',
    components: {
        inputInfo,
        identifyCardPage,
        process,
        result
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            businessTypeCode: 'businessTypeCode'
        }),
        ...mapState('permanent', {
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        }),
        ...mapState('accumulation', {
            account: 'account',
            startTime: 'startTime',
            endTime: 'endTime'
        })
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        ...mapMutations('accumulation', ['setResult']),
        backPre () {
            if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialSecond || this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialFourth) {
                this.changeStep('inputInfo');
            } else {
                this.changeStep('identifyCardPage');
            }
        },
        async handleBusiness () {
            let res = {};
            if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialThird) {
                res = await statisticalIdentity({
                    idNum: this.identifyCard.certNumber, // 身份证号码
                    identifiedPhoto: cancelPrefix(this.identifyCard.identityPic), //  照片base64
                    type: 'loan'// 业务类型：loan - 公积金贷款账户基本信息   basic - 公积金个人账户基本信息
                });
            } else if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialFifth) {
                res = await statisticalIdentity({
                    idNum: this.identifyCard.certNumber, // 身份证号码
                    identifiedPhoto: cancelPrefix(this.identifyCard.identityPic), //  照片base64
                    type: 'basic'// 业务类型：loan - 公积金贷款账户基本信息   basic - 公积金个人账户基本信息
                });
            } else {
                res.code = '0';
                res.result = {};
            }

            if (res && res.code === '0') {
                this.id = res.result.id;
                this.getResult();
            } else {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        },
        async getResult () {
            let res = {};
            if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialThird) {
                res = await checkSocialThird({
                    paperId: this.identifyCard.certNumber,
                    sPage: 1
                });
            } else if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialFifth) {
                res = await checkSocialFifth({
                    paperId: this.identifyCard.certNumber,
                    sPage: 1
                });
            } else {
                this.changeStep('down');
                return;
            }

            if (res && res.code === '0') {
                this.setResult(res.result);
                this.changeStep('down');
            } else {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        }
    },
    mounted () {
        if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialSecond) {
            this.setSteps([
                {
                    name: '输入信息',
                    isActive: true,
                    visible: true,
                    key: 'inputInfo'
                },
                {
                    name: '查询结果',
                    isActive: false,
                    visible: true,
                    key: 'process'
                },
                {
                    name: '查询结果',
                    isActive: false,
                    visible: false,
                    key: 'result'
                }
            ]);
        } else if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialThird) {
            this.setSteps([
                {
                    name: '身份识别',
                    isActive: true,
                    visible: true,
                    key: 'identifyCardPage'
                },
                {
                    name: '查询结果',
                    isActive: false,
                    visible: true,
                    key: 'process'
                },
                {
                    name: '查询结果',
                    isActive: false,
                    visible: false,
                    key: 'result'
                }
            ]);
        } else if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialFourth) {
            this.setSteps([
                {
                    name: '输入信息',
                    isActive: true,
                    visible: true,
                    key: 'inputInfo'
                },
                {
                    name: '查询结果',
                    isActive: false,
                    visible: true,
                    key: 'process'
                },
                {
                    name: '查询结果',
                    isActive: false,
                    visible: false,
                    key: 'result'
                }
            ]);
        } else if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialFifth) {
            this.setSteps([
                {
                    name: '身份识别',
                    isActive: true,
                    visible: true,
                    key: 'identifyCardPage'
                },
                {
                    name: '查询结果',
                    isActive: false,
                    visible: true,
                    key: 'process'
                },
                {
                    name: '查询结果',
                    isActive: false,
                    visible: false,
                    key: 'result'
                }
            ]);
        }
    }
};

</script>
<style lang="scss" scoped>

</style>
