<template>
    <div>
        <div class="kd-page-title">
            输入帐号信息
        </div>
        <div class="kd-page-content-wrapper wrap">
            <kd-full-input
                :ime-type="3"
                class="inputAccount"
                ref="account"
                :placeholder="title"
                :watch-data="getAccount"
            />
            <div class="inputTimeWrap">
                <el-date-picker
                    class="inputTime"
                    placeholder="起始时间"
                    v-model="sTime"
                    type="date"
                    @change="getStartTime"
                />
                <el-date-picker
                    class="inputTime"
                    placeholder="结束时间"
                    v-model="eTime"
                    type="date"
                    @change="getEndTime"
                />
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="查询"
                :button-handle="nextStep"
                :but-disable="!account && !sTime && !eTime"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import moment from 'moment';

export default {
    name: 'AccumulationInputInfo',
    data () {
        return {
            sTime: '',
            eTime: '',
            title: ''
        };
    },
    computed: {
        ...mapState('common', {
            businessTypeCode: 'businessTypeCode'
        }),
        ...mapState('permanent', {
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        }),
        ...mapState('accumulation', {
            account: 'account'
        })
    },
    mounted () {
        if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialSecond) {
            this.title = '贷款帐号';
        } else {
            this.title = '公积金帐号';
        }
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        ...mapMutations('accumulation', ['setAccount', 'setStartTime', 'setEndTime']),
        getAccount (data) {
            this.setAccount(data);
        },
        getStartTime (data) {
            this.setStartTime(moment(data).format('x'));
        },
        getEndTime (data) {
            this.setEndTime(moment(data).format('x'));
        },
        nextStep () {
            this.changeStep('down');
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        .inputAccount {
            width: 50%;
            color: #000000;
        }

        .inputTimeWrap {
            width: 50%;
            margin-top: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .inputTime {
            width: 48%;
            border-radius: 6px;
            overflow: hidden;
            height: 60px;
            box-sizing: border-box;

            ::v-deep .el-input__inner {
                width: 100%;
                height: 100%;
                background: rgba(255, 255, 255, 0.8);
                font-size: 16px;
                color: #000000;
            }
        }

    }
</style>
