<template>
    <div>
        <div class="kd-page-title">
            查询结果
        </div>
        <div
            class="kd-page-content-wrapper wrap"
            v-if="businessTypeCode === BUSINESS_EN_NAME_CODE.checkSocialThird || businessTypeCode === BUSINESS_EN_NAME_CODE.checkSocialFifth"
        >
            <text-table :data="result"/>
        </div>

        <div
            class="kd-page-content-wrapper kd-table-style noBottomMargin"
            v-if="businessTypeCode === BUSINESS_EN_NAME_CODE.checkSocialSecond"
        >
            <el-table
                :data="list"
                header-row-class-name="header-row"
                style="width: 100%"
                :highlight-current-row="true"
                border
                v-loading="loading"
            >
                <el-table-column
                    prop="loanBookNum"
                    label="序号"
                />
                <el-table-column
                    width="250"
                    label="还款日期"
                >
                    <template slot-scope="scope">
                        {{ $momentFormat(scope.row.rdate) }}
                    </template>
                </el-table-column>
                <el-table-column
                    prop="rcapi"
                    width="230"
                    label="还款本金（元）"
                />
                <el-table-column
                    prop="rinte"
                    width="230"
                    label="还款利息（元）"
                />
                <el-table-column
                    prop="sumAmt"
                    width="230"
                    label="还款总金额（元）"
                />
            </el-table>
            <div class="kd-total-content">
                <div class="left full">
                    <span :class="sPage > 1 ? '' : 'hiddenSpan'" @click="changePage(-1)"><i class="el-icon-caret-left"/>上一页</span>
                    <span>第{{ sPage }}页(共{{ totalPage }}页)</span>
                    <span :class="sPage < totalPage ? '' : 'hiddenSpan'" @click="changePage(1)">下一页<i class="el-icon-caret-right"/></span>
                </div>
            </div>
        </div>

        <div
            class="kd-page-content-wrapper kd-table-style noBottomMargin"
            v-if="businessTypeCode === BUSINESS_EN_NAME_CODE.checkSocialFourth"
        >
            <el-table
                :data="list"
                header-row-class-name="header-row"
                style="width: 100%"
                :highlight-current-row="true"
                border
                v-loading="loading"
            >
                <el-table-column
                    prop="acBookNum"
                    label="流水编号"
                />
                <el-table-column
                    prop="amt"
                    label="发生金额"
                />
                <el-table-column
                    prop="bal"
                    label="账户余额"
                />
                <el-table-column
                    width="250"
                    label="入账日期"
                >
                    <template slot-scope="scope">
                        {{ $momentFormat(scope.row.acDate) }}
                    </template>
                </el-table-column>
            </el-table>
            <div class="kd-total-content">
                <div class="left full">
                    <span :class="sPage > 1 ? '' : 'hiddenSpan'" @click="changePage(-1)"><i class="el-icon-caret-left"/>上一页</span>
                    <span>第{{ sPage }}页(共{{ totalPage }}页)</span>
                    <span
                        :class="sPage < totalPage ? '' : 'hiddenSpan'"
                        @click="changePage(1)"
                    >下一页<i class="el-icon-caret-right"/></span>
                </div>
            </div>
        </div>

        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import textTable from '@/components/text-table';
import { checkSocialSecond, checkSocialFourth } from '@/service/socialSecurity/accumulation';

export default {
    name: 'AccumulationResult',
    components: { textTable },
    data () {
        return {
            sPage: 1,
            sPages: 5,
            totalPage: 1,
            loading: false

        };
    },
    computed: {
        ...mapState('common', {
            businessTypeCode: 'businessTypeCode'
        }),
        ...mapState('accumulation', {
            result: 'result',
            list: 'list',
            account: 'account',
            startTime: 'startTime',
            endTime: 'endTime'
        }),
        ...mapState('permanent', {
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        })
    },
    mounted () {
        if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialSecond || this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialFourth) {
            this.checkList();
        }
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        ...mapMutations('accumulation', ['setResult']),
        preStep () {
            if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialSecond || this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialFourth) {
                this.changeStep('inputInfo');
            } else {
                this.changeStep('identifyCardPage');
            }
        },
        nextStep () {
            this.changeStep('down');
        },
        changePage (type) {
            // number: 1
            // numberOfElements: 10
            // totalElements: 22
            // totalPages: 3
            if (this.loading) {
                return;
            }
            if (type === -1) {
                if (this.sPage > 1) {
                    // 特殊处理，无法控制每页的数目，默认返回十个，拆分两页处理
                    this.sPage = this.sPage - 1;
                }
            } else if (type === 1) {
                if (this.sPage < this.totalPage) {
                    this.sPage = this.sPage + 1;
                }
            } else {
                return;
            }
            this.checkList();
        },
        async checkList () {
            this.loading = true;
            this.setResult({ list: [] });
            let res = {};
            if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialSecond) {
                res = await checkSocialSecond({
                    loanacNo: this.account,
                    beginDate: this.startTime,
                    endDate: this.endTime,
                    sPage: Math.ceil(this.sPage / 2),
                    sPages: this.sPages
                });
            } else if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.checkSocialFourth) {
                res = await checkSocialFourth({
                    custacNo: this.account,
                    beginDate: this.startTime,
                    endDate: this.endTime,
                    sPage: Math.ceil(this.sPage / 2),
                    sPages: this.sPages
                });
            }

            if (res && res.code === '0') {
                this.totalPage = Math.ceil(res.result.totalElements / this.sPages);

                this.setResult({
                    ...res.result,
                    sPage: this.sPage
                });
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
            this.loading = false;
        }
    }
};
</script>

<style lang="scss" scoped>

    .noBottomMargin {
        margin-bottom: 0;
    }

    .full {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
</style>
